body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{
  font-family: 'Noto Serif JP Vertical';
  src: url(../../static/media/NotoSerifJP-Regular-Vertical.cc68a87d.woff2) format('woff2'),
        url(../../static/media/NotoSerifJP-Regular-Vertical.c76213d4.woff) format('woff'),
        url(../../static/media/NotoSerifJP-Regular-Vertical.3fb8d816.otf) format('otf');
  font-weight: 400;
  font-display: swap;
}
