img {
    max-width:100%;
    height:auto;
    animation:fadeIn 0.5s ease 0s 1 normal;
	-webkit-animation:fadeIn 0.5s ease 0s 1 normal;
}

@-webkit-keyframes fadeIn {
	0%{opacity:0;}
	100%{opacity:1;}
}

@keyframes fadeIn {
	0%{opacity:0;}
	100%{opacity:1;}
}
    
